<template>
<div class="icons">
  <swiper :options="swiperOption" v-if="showSwiper">
    <swiper-slide v-for="(page, index) of pages" :key="index">
    <div class="icon" v-for="item of page" :key="item.id">
      <div class="icon-img">
        <img class="icon-image-content" :src="item.imgUrl" />
      </div>
      <p class="icon-desc">{{item.des}}</p>
    </div>
    </swiper-slide>
  </swiper>
</div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    },
    showSwiper () {
      return this.list.length
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
    width: 100%
    /*overflow: hidden*/
    /*width: 100%*/
    /*height: 0*/
    /*padding-bottom: 50%*/
  .icons
    margin-top: .2rem
    .icon
      position: relative
      overflow: hidden
      float: left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: .44rem
        box-sizing:border-box
        padding: .05rem
        .icon-image-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
        position: absolute
        left: 0
        right: 0
        bottom: .1rem
        height: .44rem
        line-height: .44rem
        text-align: center
        color: $darkTextColor
        overflow: hidden
        white-space: nowrap
        text-overflow: ellipsis
        ellipsis()
</style>



